<template>
  <v-app>
    <!-- App Bar -->
    <v-app-bar
      absolute
      dark
      app
    >
      <v-btn @click="$router.go(-1)" icon>
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      <v-app-bar-title v-if="data !== null">Detail Anime</v-app-bar-title>
      <v-skeleton-loader v-if="data === null" type="heading" width="100%"></v-skeleton-loader>
    </v-app-bar>

    <!-- Main -->
    <v-main>
      <!-- Container -->
      <v-container fluid>
        <div v-if="data !== null">
          <v-row>
            <v-col cols="12" md="4">
              <center>
                <v-img :src="data.cover" class="anime-cover"></v-img>
                <div class="anime-title">
                  <span>{{ data.title }}</span>
                </div>
              </center>
            </v-col>

            <v-col cols="12" md="8">
              <v-tabs :show-arrows="true" v-model="tab">
                <v-tab><v-icon class="mr-2">mdi-information</v-icon> Info</v-tab>
                <v-tab><v-icon class="mr-2">mdi-play</v-icon> Trailer</v-tab>
                <v-tab><v-icon class="mr-2">mdi-view-list</v-icon> Episode</v-tab>
                <v-tab><v-icon class="mr-2">mdi-account</v-icon> Karakter</v-tab>
              </v-tabs>

              <v-tabs-items v-model="tab">
                <v-tab-item>
                  <v-card flat>
                    <v-card-text>
                      <ul>
                        <li><b>Rating</b> : {{ data.rate }}</li>
                        <li><b>Status</b> : {{ data.details.status }}</li>
                        <li><b>Studio</b> : {{ data.details.studio }}</li>
                        <li><b>Adaptasi</b> : {{ data.details.adaptation }}</li>
                        <li><b>Durasi</b> : {{ data.details.duration }}</li>
                        <li><b>Tipe</b> : {{ data.details.type }}</li>
                        <li><b>Musim</b> : {{ data.details.season }}</li>
                        <li><b>Dirilis Pada</b> : {{ data.details.release_at }}</li>
                        <li>
                          <b>Genre</b> : <router-link v-for="genre in data.genres" :to="{ path: '/genre/'+genre.id }" :key="genre.id">
                              {{ genre.name }}
                            </router-link>
                        </li>
                      </ul>
                      <hr style="margin: 10px;" />
                      <span style="margin: 10px;">{{ data.synopsis }}</span>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
                <v-tab-item>
                  <v-card flat>
                    <v-card-text class="text-center embed">
                      <iframe :src="data.trailer" class="embed-video" allowfullscreen></iframe>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
                <v-tab-item>
                  <v-card flat>
                    <v-card-text class="text-center">
                      <router-link v-for="episode in data.episodes" :to="{ path: '/episode/'+episode.id }" :key="episode.id">
                        <v-btn class="episode-btn">{{ episode.name }}</v-btn>
                      </router-link>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
                <v-tab-item>
                  <v-card-text>
                    <ul>
                      <li v-for="character in data.characters" :key="character">
                        {{ character }}
                      </li>
                    </ul>
                  </v-card-text>
                </v-tab-item>
              </v-tabs-items>
            </v-col>
          </v-row>
        </div>
      </v-container>
    </v-main>
  </v-app>
</template>

<style scoped>
.anime-cover {
  width: 200px;
  height: 250px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.anime-title {
  font-weight: bold;
  font-size: 19px;
}

.embed {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;
}

.embed-video {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.episode-btn {
  margin: 5px;
  width: 45%;
}
</style>

<script>
import Vue from "vue";
import Embed from "v-video-embed";
import axios from "axios";

Vue.use(Embed);
export default Vue.extend({
  name: "Anime",
  data() {
    return {
      anime_id: this.$route.params.anime_id,
      data: null,
      tab: '',
    }
  },
  async created() {
    await axios.get("https://api.abpay.xyz/api/anime?api_key=b988b287-c0ff-408c-b941-9619f283db76&anime_id="+this.anime_id)
      .then(res => {
        this.data = res.data.data;
      }).catch(err => {
        console.log(err);
      });
  },
});
</script>